export const categoryOptions = [
    { label: 'Obleenie a obuv', value: 1 },
    { label: 'Hraky a hry', value: 2 },
    { label: 'Knihy', value: 3 },
    { label: 'Nbytok a bvanie', value: 4 },
    { label: 'Staroitnosti a zberatestvo', value: 5 },
    { label: 'Starostlivos o zvierat', value: 6 },
    { label: 'port', value: 7 },
    { label: 'Hudba', value: 8 },
    { label: 'Dom a zhrada', value: 9 },
    { label: 'Zdravie', value: 10 },
    { label: 'perky a dekorcie', value: 11 },
    { label: 'Mobily a tablety', value: 12 },
    { label: 'Potae a TV', value: 13 },
    { label: 'Elektro', value: 14 },
    { label: 'Ostatn', value: 15 },
].sort((a, b) => {
    // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/sort
    const nameA = a.label.toUpperCase(); 
    const nameB = b.label.toUpperCase(); 
    if (nameA < nameB) { return -1; }
    if (nameA > nameB) { return 1; }
    return 0;   // names must be equal
})